<script setup>
import { NButton } from "naive-ui";
import { getDevices } from "../common/index";
import { useStore } from "@/pinna/index";
import { storeToRefs } from "pinia";

const main = useStore();
const { device } = storeToRefs(main);
const connectDevice = async () => {
  // 
  await navigator.hid.requestDevice({
    filters: [
      {
        vendorId: "0x1915",
        productId: "0x072C",
      },
      {
        vendorId: "0x1915",
        productId: "0x0727",
      },

      // 
      {
        vendorId: "0x1915",
        productId: "0x07A1",
      },
      {
        vendorId: "0x1915",
        productId: "0x07A0",
      },
      // 
      {
        vendorId: "0x1915",
        productId: "0x0727",
      },
      {
        vendorId: "0x1915",
        productId: "0x0728",
      },
      // 
      {
        vendorId: '0x1915',
        productId: '0x07A2'
      }
    ],
  });
  if (!device.value) {
    location.reload();
  }
  // 
  await getDevices();
};
</script>

<template>
  <div class="index">
    <h1></h1>
    <p>
      
    </p>
    <div>
      <n-button color="#8a2be2" @click="connectDevice">  </n-button>
    </div>
  </div>
</template>

<style lang="less" scoped>
.index {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  padding: 2em 24px 24px;
  height: 100%;
  width: 100%;
  background-color: #fdf8fd;
}
</style>
