<script setup>
import {
  NSlider,
  NRadio,
  NSwitch,
  NCheckbox,
  NRadioGroup,
  NInputNumber,
  NSelect,
  NButton,
  useMessage,
  NPopover,
  NInput,
} from "naive-ui";
import { reqInitData, KEY_MAP } from "../baseData/system";
import { reportParams, mouseDevice } from "@/common";
import { ref, onMounted, watch, onUnmounted } from "vue";
import { useStore } from "@/pinna/index";
import { storeToRefs } from "pinia";
import { getCheckSum, sendReport, isLightMouse } from "../common/index.js";
import { language } from "@/baseData/language";
const main = useStore();
const message = useMessage();
const { device, init, languageValue } = storeToRefs(main);
const options = [
  { label: "1", value: 1, _label: "1" },
  { label: "2", value: 2, _label: "2" },
  { label: "3", value: 3, _label: "3" },
  // { label: "4", value: 4 },
  { label: "5", value: 5, _label: "5" },
  { label: "10", value: 10, _label: "10" },
  { label: "20", value: 20, _label: "20" },
  { label: "30", value: 30, _label: "30" },
];
onUnmounted(() => {
  main.initData("");
  main.addDpi("");
});
const defaultValue = ref([]);
const value = ref(0);
const disabled = ref(true);
const moveRate = ref(0);
const circleValue = ref(1);
const scrollNumber = ref(1);
// const selectHeight = ref(0);
const sensorCheckbox = ref([false, false, false]);
const scrollDirect = ref(0);
const mode = ref(false);
const wakeMode = ref([false, true]);
const defaultTime = ref(10);
const angle = ref(0); // 
// const changeLODheight = (value) => {
//   selectHeight.value = value;
// };
onMounted(async () => {
  if (device.value) {
    // 
    let { reportId, reportData } = reportParams(
      reqInitData + getCheckSum(reqInitData)
    );
    // 
    await device.value.sendReport(reportId, reportData);
  }
});
onUnmounted(() => {
  main.initData("");
});
const currentDpi = ref(0);
watch(init, (newValue, oldValue) => {
  if (defaultValue.value.length || !newValue) {
    // console.log("");
    return;
  }

  const arr = newValue.split(" ");
  currentDpi.value = Number(arr[6]);
  const num = mouseDevice() === "v2" ? 8 : 0;
  defaultValue.value = [num === 8 ? "00 04 00 a5 02" : "00 04 00 ad 02"].concat(
    arr.slice(5, arr.length - 1)
  );
  const array = Object.keys(KEY_MAP);
  for (let i = 0; i < array.length; i++) {
    if (KEY_MAP[array[i]].toLowerCase() === arr[5].toLowerCase()) {
      for (let j = 0; j < 3; j++) {
        if (array[i][j] === "0") {
          sensorCheckbox.value[j] = false;
        } else {
          sensorCheckbox.value[j] = true;
        }
      }
      scrollDirect.value = Number(array[i][3]);
      mode.value = array[i][4] === "0" ? false : true;
      break;
    }
  }

  if (Number(arr[6]) >= 4) {
    mode.value = true;
  }

  // LOD
  // selectHeight.value = arr[37] === "01" ? 1 : 0;
  // 
  scrollNumber.value = parseInt(arr[48 - num], 16);
  // 
  defaultTime.value =
    (parseInt(arr[44 - num], 16) + parseInt(arr[45 - num], 16) * 256) / 60;
  // 
  switch (arr[39 - num]) {
    case "00":
      wakeMode.value = [true, true];
      break;
    case "02":
      wakeMode.value = [true, false];
      break;
    case "01":
      wakeMode.value = [false, true];
      break;
    case "03":
      wakeMode.value = [false, false];
      break;
  }

  // 
  if (isLightMouse() || mouseDevice() === 'v2') {
    const angleValue = parseInt(arr[38 - num], 16);
    if (angleValue >= 0 && angleValue <= 44) {
      angle.value = angleValue;
    } else {
      angle.value = parseInt("ffffff" + arr[38 - num], 16) - Math.pow(2, 32);
    }
    isRotate.value = angle.value === 0 ? false : true;
  }
});
const setting = () => {
  // LOD
  // defaultValue.value[33] = selectHeight.value === 0 ? "02" : "01";
  const num = mouseDevice() === "v2" ? 8 : 0;
  // 
  let key = "";
  for (let i = 0; i < sensorCheckbox.value.length; i++) {
    key += Number(sensorCheckbox.value[i]);
  }
  key += scrollDirect.value;
  key += Number(mode.value);
  // console.log("key", key);
  defaultValue.value[1] = KEY_MAP[key];
  //  ['03', '02', '01', '00']
  if (wakeMode.value[0] && wakeMode.value[1]) {
    defaultValue.value[35 - num] = "00";
  } else if (wakeMode.value[0]) {
    defaultValue.value[35 - num] = "02";
  } else if (wakeMode.value[1]) {
    defaultValue.value[35 - num] = "01";
  } else {
    defaultValue.value[35 - num] = "03";
  }
  // ()
  defaultValue.value[44 - num] = scrollNumber.value
    .toString(16)
    .padStart(2, "0");

  // 
  defaultValue.value[40 - num] = ((defaultTime.value * 60) % 256)
    .toString(16)
    .padStart(2, "0");
  defaultValue.value[41 - num] = Math.floor((defaultTime.value * 60) / 256)
    .toString(16)
    .padStart(2, "0");
  defaultValue.value[42 - num] = defaultValue.value[40 - num];
  defaultValue.value[43 - num] = defaultValue.value[41 - num];

  //  
  if (isLightMouse() || mouseDevice() === 'v2') {
    defaultValue.value[34 - num] = (Math.pow(2, 32) + Number(angle.value))
      .toString(16)
      .slice(-2);
  }

  const str = defaultValue.value.join("");
  const value = str + getCheckSum(str);
  try {
    sendReport(value);
    message.success("!");
  } catch {
    message.error(",");
  }
};

onMounted(() => {
  if (document.getElementById("slider")) {
    document.getElementById("slider").style.zoom = 1 / document.body.style.zoom;
  }
});
const angleMouse = ref();
const isRotate = ref(true);
watch(angle, (newValue) => {
  const value = Number(newValue);
  if (!newValue) {
    angle.vlaue = 0;
  }
  if (value >= 45) {
    angle.value = 44;
  }
  if (value < -44) {
    angle.value = -44;
  }
  if (angleMouse.value) {
    angleMouse.value.style.transform = `rotate(${newValue}deg)`;
  }
});
const onAngleSwitch = (value) => {
  if (!value) {
    angle.value = 0;
  }
};
</script>

<template>
  <div class="system">
    <div class="linear" style="margin-top: 24px"></div>
    <div class="top">
      <n-popover>
        <template #trigger>
          <img
            src="@/images/system.png"
            alt=""
            style="cursor: pointer; margin-right: 48px"
          />
        </template>
        <span>{{ language[languageValue][""] }}</span>
      </n-popover>

      <span class="middle">{{ language[languageValue][""] }}</span>
    </div>
    <div class="linear"></div>
    <div style="box-sizing: border-box; position: relative">
      <div class="system_setting">
        <!--   -->
        <div v-if="isLightMouse() || mouseDevice() === 'v2'">
          <div style="display: flex; justify-content: space-between">
            <div>
              <span class="title">
                {{ language[languageValue][""] }}
              </span>
              <n-popover trigger="hover">
                <template #trigger>
                  <img
                    src="@/images/rate-icon2.png"
                    alt=""
                    style="
                      cursor: pointer;
                      vertical-align: middle;
                      margin-top: -3px;
                      margin-left: 4px;
                    "
                  />
                </template>
                <span>{{
                  language[languageValue][
                    ","
                  ]
                }}</span>
              </n-popover>
            </div>

            <n-switch
              v-model:value="isRotate"
              size="small"
              style="
                margin-right: 10px;
                background-color: #ccc;
                border-radius: 7px;
                margin-top: 4px;
              "
              @update-value="onAngleSwitch"
            />
          </div>
        </div>
        <div class="angle" v-if="isLightMouse() || mouseDevice() === 'v2'">
          <img src="@/images/angleMouse.png" alt="" ref="angleMouse" />
        </div>
        <div
          style="
            display: flex;
            justify-content: center;
            margin: 2px 0 10px -20px;
          "
          v-if="isLightMouse() || mouseDevice() === 'v2'"
        >
          <n-input-number
            :disabled="!isRotate"
            class="angle_input"
            v-model:value="angle"
            placeholder=""
            size="tiny"
            :show-button="false"
            style="
              width: 40px;
              background: linear-gradient(
                90deg,
                rgba(37, 91, 103, 1),
                rgba(37, 91, 103, 1)
              );

              border-radius: 3px;
              text-align: center;
            "
          >
          </n-input-number>
        </div>

        <div style="width: 90%" v-if="isLightMouse() || mouseDevice() === 'v2'">
          <n-slider
            :disabled="!isRotate"
            class="angle_slider"
            :tooltip="false"
            :min="-44"
            :max="44"
            id="slider"
            v-model:value="angle"
          ></n-slider>
          <div
            style="
              display: flex;
              justify-content: space-between;
              margin-left: -5px;
            "
          >
            <span>-44</span>
            <span>0</span>
            <span>44</span>
          </div>
        </div>

        <div class="line" v-if="isLightMouse() || mouseDevice() === 'v2'"></div>

        <div>
          <div style="margin-bottom: 15px">
            <span class="title">{{
              language[languageValue]["Sensor"]
            }}</span>
          </div>
          <div>
            <n-checkbox v-model:checked="sensorCheckbox[0]">
              {{ language[languageValue][""] }}
            </n-checkbox>
          </div>
          <div style="margin: 15px 0">
            <n-checkbox v-model:checked="sensorCheckbox[1]">{{
              language[languageValue][""]
            }}</n-checkbox>
          </div>
          <div style="margin-bottom: 15px">
            <n-checkbox v-model:checked="sensorCheckbox[2]">{{
              language[languageValue]["Motion Sync"]
            }}</n-checkbox>
          </div>
        </div>
        <div class="line"></div>
        <div class="scroll-setting" style="margin-bottom: 15px">
          <div class="title" style="margin-bottom: 10px">
            {{ language[languageValue][""] }}
          </div>

          <n-radio-group v-model:value="scrollDirect" name="radiogroup">
            <n-radio :value="0">{{ language[languageValue][""] }}</n-radio>
            <n-radio :value="1">{{ language[languageValue][""] }}</n-radio>
          </n-radio-group>
        </div>
        <div class="line"></div>
        <div>
          <div style="display: flex; justify-content: space-between">
            <span class="title">{{ language[languageValue][""] }}</span>
            <n-switch
              :disabled="currentDpi <= 3 ? false : true"
              v-model:value="mode"
              size="small"
              style="
                margin-right: 10px;
                background-color: #ccc;
                border-radius: 7px;
                margin-top: 4px;
              "
            />
          </div>
          <div class="desc">
            {{
              language[languageValue][
                "2K/4K/8K"
              ]
            }}
          </div>
        </div>
        <div class="line" style="margin-top: 15px"></div>
        <div v-if="!isLightMouse()">
          <div style="display: flex; justify-content: space-between">
            <span style="flex: 2" class="title">{{
              language[languageValue][""]
            }}</span>
            <n-input-number
              style="width: 90px"
              v-model:value="scrollNumber"
              size="tiny"
              :min="0"
              :max="20"
            />
          </div>
        </div>
        <div class="line" style="margin-top: 15px" v-if="!isLightMouse()"></div>
        <div v-if="!isLightMouse()">
          <div style="margin: 15px 0" class="title">
            {{ language[languageValue][""] }}
          </div>
          <div style="display: flex; justify-content: space-between">
            <n-checkbox v-model:checked="wakeMode[0]">{{
              language[languageValue][""]
            }}</n-checkbox>
            <n-checkbox v-model:checked="wakeMode[1]">{{
              language[languageValue][""]
            }}</n-checkbox>
          </div>
        </div>
        <div class="line" style="margin-top: 15px" v-if="!isLightMouse()"></div>
        <div>
          <div style="display: flex; justify-content: space-between">
            <span style="flex: 2" class="title">{{
              language[languageValue][""]
            }}</span>
            <n-select
              placeholder=""
              :options="
                options.map((item) => ({
                  ...item,
                  label: language[languageValue][item.label],
                }))
              "
              style="width: 90px"
              v-model:value="defaultTime"
              size="tiny"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="btn" @click="setting">
      <n-button type="info">{{ language[languageValue][""] }}</n-button>
    </div>
  </div>
</template>

<style lang="less" scoped>
.system {
  font-family: PingFang;
  width: 288px;

  color: #fff;
  position: relative;
  .angle {
    // position: relative;
    display: flex;
    justify-content: center;
    margin-left: -20px;
    background: url('@/images/line.png') no-repeat;
    background-position: 65px;
    img {
      transform: rotate(0deg);
    }
  }
  // .angle_input {
  //   /deep/ .n-input__input-el {
  //     color: #fff !important;
  //   }
  // }
  .angle_slider {
    /deep/ .n-slider-rail__fill {
      background-color: #fff;
    }
  }
  .scroll-setting {
    /deep/ .n-radio__label {
      color: #637077;
      font-weight: bold;
      font-size: 14px;
    }
    /deep/ .n-radio--checked .n-radio__label {
      color: #fff;
    }
  }
  .desc {
    font-family: PingFang;
    font-weight: 500;
    font-size: 11px;
    color: #637077;
    margin-top: 13px;
  }
  .word {
    font-weight: bold;
    font-size: 14px;
    color: #fff;
  }
  .title {
    font-weight: bold;
    font-size: 14px;
    color: #b5d8f1;
  }
  .linear {
    width: 288px;
    height: 3px;
    background: linear-gradient(90deg, #457287, #387d8c, #385d65);
  }
  .top {
    display: flex;
    align-items: center;
    background: linear-gradient(66deg, #020612, #16221e);
    margin-bottom: 16px;

    height: 64px;
    border-bottom-right-radius: 15px;
    border-bottom-left-radius: 15px;
    padding: 0 18px 0 26px;
    .middle {
      position: relative;
      font-family: "PingFang";
      font-weight: bold;
      font-size: 17px;
      color: #b5d8f1;
    }
  }
  .header {
    height: 50px;
    line-height: 50px;
    border-radius: 10px;
    text-align: center;
    background-color: #15211d;
  }
  .system_setting {
    height: 600px;
    overflow: auto;
    overflow-x: hidden;
    padding: 23px 13px 24px 19px;
    background: linear-gradient(
      90deg,
      rgba(19, 52, 71, 0.22),
      rgba(2, 12, 22, 0.22)
    );
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    /deep/ .n-checkbox__label {
      color: #637077;
      font-weight: bold;
      font-size: 14px;
    }
    /deep/ .n-checkbox--checked .n-checkbox__label {
      font-weight: bold;
      font-size: 14px;
      color: #fff;
    }
  }
  /deep/ .n-radio__label,
  /deep/ .n-checkbox__label {
    color: #fff;
  }
  .selectedIcon::before {
    position: absolute;
    left: -50px;
    content: "";
    width: 0;
    height: 0;
    border: 8px solid transparent;
    border-left-color: #fff;
  }
  .btn {
    position: absolute;
    right: 10px;
    margin-top: 20px;
    /deep/.n-button {
      background: linear-gradient(
        90deg,
        rgba(19, 52, 71, 0.22),
        rgba(2, 12, 22, 0.22)
      );
    }
  }
}
.line {
  width: 260px;
  height: 2px;
  background: linear-gradient(90deg, #000c1a, #255b67);
  margin-left: -8px;
  margin-bottom: 15px;
}
</style>
