export const reportParams = (value) => {
  if (isWireless()) {
    value = value.replace(/[^0-9a-fA-F]/g, "");
    value = value.slice(0, 2) + "4" + value.slice(3);
  }
  let data = parseHexArray(value);

  let reportId = data.getUint8(0);
  let reportData = new Uint8Array(data.buffer).slice(1);
  return {
    reportId,
    reportData,
  };
};

// uint8
const negToUint8 = (num) => {
  // 32
  let bits = new Uint32Array(1);
  bits[0] = num;

  // Uint8Array
  let uint8 = new Uint8Array(bits.buffer);
  return uint8[0];
};

export const getCheckSum = (strData, maxSize = 63) => {
  if (isWireless()) {
    strData = strData.replace(/[^0-9a-fA-F]/g, "");
    strData = strData.slice(0, 2) + "4" + strData.slice(3);
  }

  // console.log("data", strData);
  let data = parseHexArray(strData);

  let pDataArray = new Uint8Array(data.buffer).slice(1);

  let sum = 0;
  for (let i = 0; i < maxSize; i++) {
    sum += pDataArray[i];
  }
  sum %= 256;
  // console.log("sum", sum);
  let value = parseInt("A1", 16) - sum;

  // console.log(value.toString(16))
  value = value >= 0 ? value : negToUint8(value);

  return value.toString(16).padStart(2, "0");
};

export const sendReport = async (value) => {
  let save = saveData;
  if (isWireless()) {
    value = value.replace(/[^0-9a-fA-F]/g, "");
    value = value.slice(0, 2) + "4" + value.slice(3);
    save = save.replace(/[^0-9a-fA-F]/g, "");
    save = save.slice(0, 2) + "4" + save.slice(3);
  }
  // console.log('save',save)
  save += getCheckSum(save);

  const main = useStore();
  const { device, init } = storeToRefs(main);

  let { reportId, reportData } = reportParams(value);

  // 
  await device.value.sendReport(reportId, reportData);

  // console.log("value", a);
  setTimeout(() => {
    let data = reportParams(save);
    device.value.sendReport(data.reportId, data.reportData);
  }, 1000);
};

// xml
export const getXMLData = (event) => {
  if (!event.target.files[0]) return;
  return new Promise((resolve, reject) => {
    const file = event.target.files[0];
    const reader = new FileReader();

    reader.onload = (e) => {
      try {
        const xmlData = e.target.result;
        const jsonData = convert.xml2json(xmlData, {
          compact: true,
          spaces: 4,
        });
        const newData = JSON.parse(jsonData);
        resolve(newData);
      } catch (err) {
        reject("");
      }
    };
    reader.readAsText(file);
  });
};

// xml
export const exportXml = (
  macroinfo,
  type = "macro",
  fileName = "macroinfo"
) => {
  console.log(macroinfo);
  const rootElement = {
    [type]: macroinfo,
  };
  const xmlData = convert.js2xml(rootElement, {
    compact: true,
    ignoreComment: true,
    spaces: 2,
  });

  const xmlBlob = new Blob([xmlData], { type: "text/xml" });
  const exportLink = document.createElement("a");
  exportLink.href = URL.createObjectURL(xmlBlob);
  if (fileName === "macroinfo") {
    exportLink.download = macroinfo[fileName]._attributes.macro_name + ".xml";
  } else {
    exportLink.download = macroinfo[fileName]._attributes.name + ".xml";
  }

  exportLink.click();
};
// ,
export const isWireless = (paramId) => {
  if (paramId) {
    return paramId === 1831 || paramId === 1832 ? true : false;
  }
  const main = useStore();
  const { device } = storeToRefs(main);
  // console.log(device.value.productId);
  if (device.value) {
    const id = Number(device.value.productId);
    return id === 1831 || id === 1832 ? true : false;
  }
  return false;
};

// 8k
export const isOGM8K = () => {
  const main = useStore();
  const { device } = storeToRefs(main);
  if (device.value) {
    const id = Number(device.value.productId);
    return id === 1832 ? true : false;
  }
  return false;
};
// 
export const mouseDevice = () => {
  const main = useStore();
  const { device } = storeToRefs(main);
  if (device.value) {
    const id = Number(device.value.productId);
    switch (id) {
      case 1836:
      case 1831:
        return "cloud";

      case 1832:
      case 1953:
      case 1952:
      case 1954:
        return "v2";
      default:
        return "cloud";
    }
  }
  return "cloud";
};
// 
export const isLightMouse = () => {
  // if(isLight) return true

  const main = useStore();
  const { device, currentMouse } = storeToRefs(main);
  if (currentMouse.value === "OGM V2 GV") return true;
  if (device.value) {
    const id = Number(device.value.productId);
    return id === 1954 ? true : false;
    // return [1952, 1954, 1832].indexOf(id) !== -1 ? true : false;
    // return [1952,1953,1954,1832].indexOf(id) !== -1 ? true : false;
  }
  return false;
};
