<script setup>
import Home from "@/views/home.vue";
import { NMessageProvider } from "naive-ui";
import { onMounted, onUnmounted } from "vue";
let timer = null;
const isScale = () => {
  if (parseInt(window.devicePixelRatio) === window.devicePixelRatio) {
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      const deviceWidth = document.documentElement.clientWidth / 1920;
      const deviceHeight = document.documentElement.clientHeight / 1080;
      const scale = deviceWidth > deviceHeight ? deviceHeight : deviceWidth;

      document.body.style.zoom = scale > 1 ? 1 : scale;
      if (document.getElementById("slider")) {
        document.getElementById("slider").style.zoom =
          1 / document.body.style.zoom;
      }
    }, 200);
  }
};

onMounted(() => {
  isScale();
  window.addEventListener("resize", isScale);
});
onUnmounted(() => {
  window.removeEventListener("resize", isScale);
});
</script>

<template>
  <n-message-provider>
    <Home />
  </n-message-provider>
</template>

